.list{
    color: white;
    background-color:#daba1b ;
    width: 80%;
    height: 50px;
    margin: 20px auto;
    border-radius: 30px;
}
.list p{
    padding: 10px;
    position: relative;
}
.list p span{
    position: absolute;
    right: 20px;
    cursor: pointer;
}
.list p input{
    background-color: transparent;
    border: none;
    color: white;
    font-size: 18px;
    word-wrap: break-word;
    word-break: break-all;
    margin: 0%;
}