body{
  background-color: antiquewhite;

}
.container{
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-items: center;
  align-items: center;
}
.App{
  background-color: #333;
  width: 400px;
  height:60%;
  margin: auto;
  border-radius: 30px;
}
header{
  width: 100%;
  height: 70px;
  position: relative;
  margin: 10px;
}
#to-do-form input{
  background-color:  #c97402 ;
  border: 0px;
  width: 70%;
  height: 50px;
  margin:10px;
  border-radius: 30px;
  font-size: 18px;
  text-align: center;
  color:white;
}
#to-do-form input::placeholder{
  color: white;
}
#to-do-form button{ 
  width: 20%;
  height: 50px;
  border:none;
  border-radius:20px;
  cursor : pointer;
  
}
